<script>
import axios from "axios";
export default({
    name: 'detectionfriend',
    data() {
        return {
            step:1,
        }
    },
    methods: {
        prev() {
            this.step--;
        },
        next() {
            console.log('dd');
            this.step++
            
        },
        coreCount(){
            this.step++
            var cores=0;
            var qestions=document.getElementsByClassName("question")
            for(var i=0;i<qestions.length;i++)
            {
                var count=0;
                var flag=1;
                var answer=document.getElementsByName("q"+(i+1));
                for(var j=0;j<answer.length;j++)
                {
                    if(answer[j].checked)
                    {
                        count+=answer[j].value*1;
                        console.log(count)
                    }
                }
                if(flag==1)
                    cores+=count;
                    console.log(cores);
                if(cores >= 76){
                    document.getElementById('dectext').innerHTML='糟糕，你被情緒勒索的程度很高喔！情緒勒索的戲碼一而再再而三地重複上演，你必須開始思考自己在關係中的定位以及如何讓自己更開心，並努力嘗試改善關係狀態喔～';
                    document.getElementById('highimg').src='static/高.png';
                }else if(cores >= 51 && cores <= 75){
                    document.getElementById('dectext').innerHTML='哎呀，你被情緒勒索的程度介於中間！你很容易受到各種方式的情緒勒索。建議你隨時注意、觀察自己的狀態，以避免深陷於勒索的情況喔～';
                    document.getElementById('highimg').src='static/中.png';
                }else if(cores >= 26 && cores <= 50){
                    document.getElementById('dectext').innerHTML='耶嘿，你被情緒勒索的程度很低喔！你可能經歷過幾段被勒索的關係，但似乎成功逃離了呢～';
                    document.getElementById('highimg').src='static/輕.png';
                }else if(cores < 25){
                    document.getElementById('dectext').innerHTML='恭喜你，你沒有被情緒勒索困擾喔！你不是容易被欺負的對象，你所處的關係中幾乎沒有情緒勒索的出現～';
                    document.getElementById('highimg').src='static/健康.png';
                }  
            }	
        }
    },
})



</script>

