                                <div class="col-lg-12">
                                    <h5>(1)停止動作</h5>
                                    <p>先別急著回應任何需求，留點空間讓自己思考。愛無法拯救暴力，能拯救自己的就是離開暴力現場。有意識地判別自己承受的是精神虐待還是合理的教養。</p>
                                    <div class="container">
                                        <div class="row">
                                            <div class="col-lg-6">
                                                <p class="conversation">「你不是一直很想搬離開家去住嗎？如果你去讀醫學系的話，我就讓你搬出去住。」</p>
                                                <img class="b4" src="static/fog/section4/deerfather.png">
                                            </div>
                                            <div class="col-lg-6">
                                                <p class="conversation">「爸，我現在還沒想清楚，我們之後再談這件事好嗎？」</p>
                                                <img class="b4" src="static/fog/deer.png">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-lg-12">
                                    <h5>(2)認真思考 / 拒絕害怕</h5>
                                    <p>如果順從，對我會有什麼負面影響？ 保存關係中的完整自我，才有可能得到真正的親密感。<br>別容許任何對你身心有害的事物進入你的生活。</p>
                                    <div class="container center">
                                        <div class="col-lg-8">
                                            <p class="conversation">「如果聽爸爸的話去讀那我肯定不會開心嗎。畢竟現在每天讀書的日子我都快受不了了，將來一定會更慘。我絕對不要這樣。」</p>
                                            <img class="b4" src="static/fog/deer.png">
                                        </div>
                                    </div>
                                </div>
                                <div class="col-lg-12">
                                    <h5>(3)理性溝通</h5>
                                    <p>知道自己要什麼，有意識的讓步、有條件的yes。<br>以成熟的心態面對，不無理取鬧、不一昧抵抗，只是純粹單純的告訴對方自己對事件的看法，不加入任何反擊的話語。</p>
                                    <div class="container">
                                        <div class="row">
                                            <div class="col-lg-6">
                                                <p class="conversation">「爸，術業有專攻，現在這個世代不是只有當醫生才是好的。我是真的很喜歡劇場這一塊，想要深入研究、以後當個劇場演員。我可以靠這個養活自己，當醫生我真的不會快樂。」</p>
                                                <img class="b4" src="static/fog/deer.png">
                                            </div>
                                            <div class="col-lg-6">
                                                <p class="conversation">「好吧，我也只是想要你能有個好的未來。既然你有明確的目標、想要追尋夢想，那就去吧，我不會再干涉你了。」</p>
                                                <img class="b4" src="static/fog/section4/deerfather.png">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <i class="fas fa-arrow-down"></i>
                    <div class="box">
                        <h4>堅定信念</h4>
                        <p>良好的關係是建立在：理解、尊重、溝通之上的。</p>
                        <div class="container center">
                            <div class="col-lg-7">
                                <p class="conversation">「好好的溝通沒有那麼難嘛，原來只要我願意好好的向爸爸表達想法，他是可以接受的！以前總是不敢，就怕爸爸聽不進去，原來只要讓他看見我的努力跟決心，他就會支持我了！」</p>
                                <img class="b4" src="static/fog/deer.png">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <p>其實，不管處在哪種關係都是同樣的道理。都會有不管再怎麼努力回應對方的需求，卻發現達不到期望，而貶低自我。我們必須明白，每個人都是獨一的個體、不是別人的附屬。一昧的放縱別人予取予求，久而久之，別人也不會把你當一回事。只有當你足夠愛自己，別人才會尊重你。<br><br>愛的真正基礎是尊重。千萬別想著情況會漸漸好轉，那是不可能的。唯有打破現狀，才能改善情況。「人生最重要的事，是在不傷害他人權益的前提下，重視自己的感受、滿足自己的需求，而非為了滿足他人期待而活。」<br><br>你永遠都有選擇的權利。你的選擇權在於『想不想要』，而不是『我害不害怕』。別忘了，你是自己生命的主人。人際關係是因你而存在，你沒有義務滿足別人的期待。如果連自己都捍衛不了自己的感受與需求，沒有人能幫的了你。不要再被以愛之名的勒索綁架了。也請記得無論何種狀況，友善溝通才是最佳解法。</p>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "FogText"

}
</script>

